body {
  background-color: ghostwhite;
}

.App {
  background-color: whitesmoke;
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
  box-shadow: 0px 0px 30px lightgray;;
}

#to-do-form input {
  background-color:whitesmoke;
  border: 1px grey solid;
  width: 200px;
  height:50px;
  padding: 0 20px;
  margin: 20px;
  font-size: 18px;
  border-radius: 10px;
  color:crimson;
}

#to-do-form input::placeholder {
  color: gray;
}

#to-do-form input:focus, #to-do-form button:focus {
  outline: none;
}

#to-do-form button {
  height: 50px;
  width: 80px;
  border-radius: 100px;
  border: 0;
  background:crimson;
  cursor: pointer;
  color: white;
}
